import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";
import { Button, Row, Col, ListGroup, Image, Card } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Message from "../Components/Message";
import Loader from "../Components/Loader";
import { getOrderDetails, payOrder } from "../actions/orderActions";
import { ORDER_PAY_RESET } from "../constants/orderConstants";

import { PayPalScriptProvider, PayPalButtons } from "@paypal/react-paypal-js";

function OrderScreen() {
  const orderId = useParams();

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const orderDetails = useSelector((state) => state.orderDetails);
  const { order, error, loading } = orderDetails;

  const orderPay = useSelector((state) => state.orderPay);
  const { loading: loadingPay, success: successPay } = orderPay;
  const [sdkReady, setSdkReady] = useState(false);

  let updatedOrder = { ...order };
  if (!loading && !error) {
    const itemsPrice = order.orderItems
      .reduce((acc, item) => acc + item.price * item.qty, 0)
      .toFixed(2);

    updatedOrder = {
      ...updatedOrder,
      itemsPrice: itemsPrice,
    };
  }

  const addPayPalScript = () => {
    const script = document.createElement("script");
    script.type = "text/javascript";
    script.src =
      "https://www.paypal.com/sdk/js?client-id=AVtqGMXmNNPpKp9GKlJ8T29fRdKoJNJEwx9wjvh596aq3Jw6q23nufs8Aq3XfKo6DZzSqZSiSAZ3zC0c&currency=USD";
    script.async = true;
    script.onload = () => {
      setSdkReady(true);
    };
    document.body.appendChild(script);
  };

  useEffect(() => {
    if (!order || successPay || order._id !== Number(orderId.id)) {
      dispatch({ type: ORDER_PAY_RESET });
      dispatch(getOrderDetails(orderId.id));
    } else if (!order.isPaid) {
      if (!window.paypal) {
        addPayPalScript();
      } else {
        setSdkReady(true);
      }
    }
  }, [dispatch, orderId.id, order, successPay]);

  const successPaymentHandler = async (paymentResult) => {
    dispatch(payOrder(orderId.id, paymentResult));
  };


  const onApproveHandler = (data, actions) => {
    return actions.order.capture().then(function (details) {
      dispatch(payOrder(orderId.id, details));
    });
  }

  const createOrderHandler = (data, actions) => {
    console.log(order);
    const purchaseUnits = order.orderItems.map(item => ({
      amount: {
        currency_code: "USD",
        value: Number(Number(item.price * item.qty) + Number(item.taxPrice) + Number(item.shippingPrice)).toFixed(2),
        breakdown: {
          item_total: { // Add item_total field
            currency_code: "USD",
            value: item.price * item.qty // Calculate total value of items in this purchase unit
          },
          tax_total: {
            currency_code: "USD",
            value: item.taxPrice
          },
          shipping: {
            currency_code: "USD",
            value: item.shippingPrice
          }
        }
      },
      description: item.name,
      reference_id: item.merchant_id,
      payee: {
        merchant_id: item.merchant_id
      },
      items: [{ // Define items array
        name: item.name,
        unit_amount: { // Specify the unit amount for the item
          currency_code: "USD",
          value: item.price // Use toFixed to fix decimal places
        },
        quantity: item.qty
      }]
    }));
    return actions.order.create({
      purchase_units: purchaseUnits 
    });
  };
  
  
  


  return (
    <>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <div>
          <h2>Order: {order._id}</h2>
          <Row>
            <Col md={8}>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <h2>Shipping</h2>
                  <p>
                    <strong>Name:</strong>
                    {order.user.name}
                  </p>
                  <p>
                    <strong>Email:</strong>
                    <a href={`mailto:${order.user.email}`}>
                      {order.user.email}
                    </a>
                  </p>
                  <p>
                    <strong>Shipping: </strong>
                    {order.shippingAddress.address},{" "}
                    {order.shippingAddress.city}
                    {"  "}
                    {order.shippingAddress.postalCode},{"  "}
                    {order.shippingAddress.country}
                  </p>

                  {order.isDelivered ? (
                    <Message variant="success">
                      Delivered on {order.deliveredAt}
                    </Message>
                  ) : (
                    <Message variant="danger">Not Delivered</Message>
                  )}
                </ListGroup.Item>

                <ListGroup.Item>
                  <h2>Payment Method</h2>
                  <p>
                    <strong>Method: </strong>
                    {order.paymentMethod}
                  </p>
                  {order.isPaid ? (
                    <Message variant="success">Paid on {order.paidAt}</Message>
                  ) : (
                    <Message variant="danger">Not Paid</Message>
                  )}
                </ListGroup.Item>

                <ListGroup.Item>
                  <h2>Order Items</h2>
                  {order.orderItems.length === 0 ? (
                    <Message variant="info">Your Cart is Empty</Message>
                  ) : (
                    <ListGroup variant="flush">
                      {order.orderItems.map((item, index) => (
                        <ListGroup.Item key={item.product}>
                          <Row>
                            <Col md={1}>
                              <Image
                                src={item.image}
                                alt={item.name}
                                fluid
                                rounded
                              />
                            </Col>
                            <Col>
                              <Link to={`/products/${item.product}`}>
                                {item.name}
                              </Link>
                            </Col>
                            <Col md={4}>
                              {item.qty} X ${item.price} = $
                              {(item.qty * item.price).toFixed(2)}
                            </Col>
                          </Row>
                        </ListGroup.Item>
                      ))}
                    </ListGroup>
                  )}
                </ListGroup.Item>
              </ListGroup>
            </Col>

            <Col md={4}>
              <Card>
                <ListGroup variant="flush">
                  <ListGroup.Item>
                    <h2>Order Summary</h2>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Item:</Col>
                      <Col>${updatedOrder.itemsPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Shipping:</Col>
                      <Col>${order.shippingPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Tax:</Col>
                      <Col>${order.taxPrice}</Col>
                    </Row>
                  </ListGroup.Item>
                  <ListGroup.Item>
                    <Row>
                      <Col>Total:</Col>
                      <Col>${order.totalPrice}</Col>
                    </Row>
                  </ListGroup.Item>

                  {!order.isPaid && (
                    <ListGroup.Item>
                      {loadingPay && <Loader />}
                      {!sdkReady ? (
                        <Loader />
                      ) : (
                        <PayPalScriptProvider
                          options={{
                            "client-id":
                              "Afu9_HTbqHClrjFBG5PXwtmPqAQbQNYHRDXnv69Os_ki7STC3mdPtM_QePIAESnUlc9MSaKgCWYv6Zs-",
                              merchantId: order.orderItems.map((item) => item.merchant_id)
                          }}
                        >
                          <PayPalButtons
                            createOrder={createOrderHandler}
                            style={{ layout: "vertical" }}
                            onApprove={onApproveHandler}
                          />
                        </PayPalScriptProvider>
                      )}
                    </ListGroup.Item>
                  )}
                </ListGroup>
              </Card>
            </Col>
          </Row>
        </div>
      )}
    </>
  );
}

export default OrderScreen;
